.p-icon--medium {
  background-size: contain;
  height: 1.25rem;
  width: 1.25rem; }

.p-icon--large {
  background-size: contain;
  height: 1.5rem;
  width: 1.5rem; }

.p-icon--x-large {
  background-size: contain;
  height: 1.75rem;
  width: 1.75rem; }

.p-icon--x-large {
  background-size: contain;
  height: 2.25rem;
  width: 2.25rem; }

.p-icon--xx-large {
  background-size: contain;
  height: 3rem;
  width: 3rem; }

[class*='p-button-'] [class*='p-icon-'] {
  top: -1px;
  vertical-align: middle; }

.p-image--bordered {
  border-color: #cdcdcd;
  border-style: solid;
  border-width: 1px; }

.p-image--shadowed {
  box-shadow: 0 1px 5px 1px rgba(205, 205, 205, 0.2); }

.p-inline-images {
  display: block;
  list-style: none;
  margin-left: 0;
  padding-left: 0;
  text-align: center; }

.p-inline-images__item {
  display: inline-block;
  margin: 1rem;
  overflow: hidden;
  text-align: center;
  vertical-align: middle; }
  @media only screen and (min-width: 772px) {
    .p-inline-images__item {
      margin: 1.875rem; } }

.p-inline-images__logo {
  max-height: 3rem;
  max-width: 7rem;
  width: auto; }
  @media screen and (min-width: 772px) {
    .p-inline-images__logo {
      max-height: 5.5rem;
      max-width: 9rem; } }

.p-label--validated {
  background-color: #006b75;
  color: #fff; }

.p-label--in-progress {
  background-color: #f99b11;
  color: #111; }

.p-label--new {
  background-color: #0e8420;
  color: #fff; }

.p-label--updated {
  background-color: #335280;
  color: #fff; }

.p-label--deprecated {
  background-color: #c7162b;
  color: #fff; }

.p-link--soft {
  color: #111; }
  .p-link--soft:visited {
    color: #111;
    text-decoration: none; }
  .p-link--soft:hover {
    color: #111111; }
  .p-link--soft.is-selected {
    font-weight: 400; }

.p-link--inverted {
  color: #f7f7f7;
  font-weight: 400; }
  .p-link--inverted:hover {
    color: #f7f7f7; }
  .p-link--inverted:visited {
    color: #dedede; }

